<template>
  <div id="question">
    <v-app-bar color="primary" dark style="margin-bottom:20px">
      <div class>
        <span v-if="prevQuestionId === undefined">
          <v-btn color="secondary" @click="gotoCourse">戻る</v-btn>
        </span>
        <span v-else>
          <v-btn color="secondary" @click="gotoPrevQuestion">戻る</v-btn>
        </span>
      </div>

      <v-spacer></v-spacer>

      <div class>
        <div v-if="nextQuestionId === undefined">
          <v-btn color="secondary" @click="gotoCourse">一覧画面に戻る</v-btn>
        </div>
        <div v-else>
          <v-btn color="secondary" @click="gotoNextQuestion">進む</v-btn>
        </div>
      </div>
    </v-app-bar>

    <!-- <div style="max-width:1000px;text-align:center " > -->
    <div class="d-flex justify-center mb-6x" style="text-align:center">
      <v-card style="padding:20px">
        <v-row style="max-width:1300px">
          <v-col class="col-12 col-md-6">
            <v-card>
              <v-card-text>
                <div align="center" ref="imageCard" id="imageCard">
                  <v-img :key="question.id" :src="question.image" style="max-width:667px"></v-img>
                  <div></div>
                </div>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col class="col-12 col-md-6">
            <v-card id="videocard">
              <video width="360" height="270" controls>
                <source :src="question.video" />Use Chrome.
              </video>
            </v-card>

            <v-card style="margin-top:30px;padding:20px;min-height:200px" id="recordcard">
              <div>
                <div v-if="recordURL !== null">
                  <div class="audio-container">
                    <div>録音結果</div>

                    <div align="center">
                      <audio id="audio" :src="recordURL" controls />
                    </div>
                  </div>
                  <v-btn
                    color="primary"
                    @click="submit"
                    :disabled="submitted"
                    style="margin-right:40px"
                  >
                    <span v-if="submitted">保存済みです</span>
                    <span v-else>保存して次へ</span>
                  </v-btn>
                  <v-btn color="lightgrey" @click="deleteRecord">録音を削除</v-btn>
                </div>
                <div v-else>
                                                <span style="margin-left: 10px;style:inline-box">{{direction}}</span><br><br>

                  <recorder mode="press" @result="onRecordingResult" />
                </div>
                <div v-if="nextQuestionId === undefined">
                  <div v-if="submitted">これが最後の問題です。お疲れ様でした。</div>
                </div>
              </div>
            </v-card>
          </v-col>
          <v-snackbar v-model="snackbar" :timeout="1000" color="primary">
            回答をサーバーに保存しました
            <v-btn text @click="snackbar = false">Close</v-btn>
          </v-snackbar>
        </v-row>
      </v-card>
    </div>

    <!-- </div> -->
  </div>
</template>
<style >
#question .my-recorder:hover {
  background-color: inherit;
}
#question .my-recorder.active {
  background-color: inherit;
}

#question .recording-now-msg {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
}
#question .recording-stop-btn {
  font-weight: bold;
}

#question .plyr--audio {
  width: 250px;
}

#question .audio-container {
  padding-bottom: 20px;
}
</style>
<script>
import recorder from '@/components/MicRecorder/VueRecordAudio.vue'
import Plyr from 'plyr'

export default {
  name: 'Question',
  components: { recorder },
  computed: {
    recordURL () {
      return this.$store.state.currentRecordSrc
    },
    direction () {
      return this.$store.getters.currentQuestionCategory.name
    },
    notSubmitted () {
      return !this.submitted
    },
    nextQuestionId () {
      let found = false
      for (var q of this.$store.getters.currentQuestionCategory.questions) {
        if (found) {
          return q.id
        }

        if (q.id === this.question.id) {
          found = true
        }
      }

      return undefined
    },

    prevQuestionId () {
      let prevQId
      for (var q of this.$store.getters.currentQuestionCategory.questions) {
        if (q.id === this.question.id) {
          return prevQId
        }

        prevQId = q.id
      }
      // make sure things are fine even if $store is empty
      return undefined
    }
  },

  methods: {
    // fetchAll: function () {
    //   this.fetchCourses()
    //   this.fetchResponses()
    // },
    onRecordingResult (data) {
      // this.recordings =
      this.$store.commit(
        'setCurrentRecordSrc',
        window.URL.createObjectURL(data)
      )
      const blob = new Blob([data], { type: 'audio/webm' })
      const fileOfBlob = new File([blob], 'numusic.webm')
      this.$store.commit('setCurrentRecord', fileOfBlob)
    },

    countResponses: function (qc) {
      if (qc.questions !== null) {
        const qAllIds = new Set(qc.questions.map(q => q.id))
        const qRespondedIdes = new Set(
          this.responses.map(res => res.question_id)
        )
        const intersect = new Set(
          [...qAllIds].filter(x => qRespondedIdes.has(x))
        )

        return intersect.size
      } else {
        return 0
      }
    },

    initialize () {
      this.question = this.$store.getters.currentQuestion

      // Handle response
      this.response = this.$store.getters.currentResponse
      if (this.response !== undefined) {
        if (this.response.audio) {
          this.$store.commit(
            'setCurrentRecordSrc',
            this.$mediaRoot + this.response.audio
          )
          this.submitted = true
        }
      }
    },

    deleteRecord () {
      this.$store.commit('resetRecord')
      this.submitted = false
    },

    submit: function () {
      // this.loading = true

      const postData = {
        qid: this.question.id,
        text: 'from vue with voice',
        audio: this.$store.state.currentRecord
      }

      this.postResponse(postData, () => {
        this.submitted = true
        this.snackbar = true
        setTimeout(() => {
          this.gotoNextQuestion()
        }, 1000)
      })
    },

    _gotoQuestionWithId (id) {
      this.deleteRecord()
      this.submitted = false
      this.question = {}
      this.$store.commit('setCurrentQuestionId', id)

      if (id === undefined) {
        this.$router.push({ name: 'Courses' })
      } else {
        this.fetchResponses()
        this.initialize()
        this.$router.push({ name: 'Question', params: { id: id } })
      }
    },

    gotoPrevQuestion () {
      this._gotoQuestionWithId(this.prevQuestionId)
    },
    gotoNextQuestion () {
      this._gotoQuestionWithId(this.nextQuestionId)
    },
    gotoCourse () {
      this._gotoQuestionWithId(undefined)
    }
  },

  created: function () {
    const st = this.$store.state
    this.$store.commit('setCurrentQuestionId', parseInt(this.$route.params.id))

    // delete recording data
    this.deleteRecord()

    // ========================================
    // Set all parent current ids if not set
    // ========================================
    const ids = [
      st.currentCourseId,
      st.currentChapterId,
      st.currentQuestionCategoryId,
      this.$route.params.id
    ]
    const allInt = ids.reduce(
      (acc, cur) => acc & (cur > 0) & Number.isInteger(cur),
      true
    )
    if (!allInt) {
      // something wrong with ID. reset it.
      this.fetchCourses(() => this.gotoCourse())
      this.fetchResponses(() => this.gotoCourse())

      let found = false
      for (var course of st.courses) {
        for (var chapter of course.chapters) {
          for (var qc of chapter.question_categories) {
            for (var question of qc.questions) {
              if (question.id === parseInt(this.$route.params.id)) {
                found = true
                const idObj = {
                  courseId: course.id,
                  chapterId: chapter.id,
                  qcId: qc.id,
                  questionId: question.id
                }
                this.$store.commit('setAllCurrentIds', idObj)
              }
            }
          }
        }
      }
      if (!found) {
        // response object seems to have a problem. go back to course list
        this.$router.push({ path: 'Courses' })
      }
    }
    this.initialize()
  },

  mounted () {
    this.audioPlayer = new Plyr('#audio', {
      controls: ['play', 'progress', 'current-time']
    })
    this.videoPlayer = new Plyr('#video')
  },

  data: () => ({
    question: {},
    response: undefined,
    submitted: false,
    snackbar: false,
    audioPlayer: {},
    videoPlayer: {}
  })
}
</script>
