<template lang="html">
<div>

    <div
    v-if="isSupported"
    :class="{
      'active': isRecording,
      'paused': isPaused
    }"
    @mousedown="startRecording"
    @mouseleave="stopRecording"
    @mouseup="stopRecording"
    @touchstart="startRecording"
    @touchend="stopRecording"
    @touchcancel="stopRecording"
    class="my-recorder"
  > <div v-if="isRecording">
      <span class="recording-now-msg"> 録音中...      <v-progress-circular
        indeterminate
        color="primary"
        size="20"
      ></v-progress-circular>
</span>
                    <v-btn color="error"  class="recording-stop-btn">録音を停止する</v-btn>

    </div>
    <div v-else>

                <v-btn color="primary" class="recording-start-btn" >録音を始める</v-btn>

      </div>

  </div>
</div>

</template>

<script>
import ElementMixin from './ElementMixin'

const supportedTypes = [
  'audio/aac',
  'audio/ogg',
  'audio/wav',
  'audio/webm'
]

export default {
  name: 'VueRecordAudio',
  mixins: [ElementMixin],
  props: {
    mimeType: {
      type: String,
      default: 'audio/webm',
      validator: v => supportedTypes.includes(v)
    }
  },
  data () {
    return {
      constraints: {
        audio: true,
        video: false
      }
    }
  }
}
</script>

<style >

.my-recorder:hover{
    background-color:#26a69a
}
.my-recorder.active{
    background-color:#ef5350;
    -webkit-animation:pulse 1.25s cubic-bezier(.66,0,0,1) infinite;
    animation:pulse 1.25s cubic-bezier(.66,0,0,1) infinite
}

</style>
